.heading-title::after { content: " "; width: 120px; display: block; margin: 0.5rem auto; border-bottom: 3px solid #ED1C24; }

.heading-title .pre-head { font-size: 1rem; color: #ED1C24; font-weight: normal; text-transform: uppercase; }

.heading-title.text-left::after { margin: 0.5rem 0; }

.card { box-shadow: 0 3px 9px rgba(0, 0, 0, 0.3); border-radius: 0.7rem; border: 0; }

.card.round { border-radius: 30px; }

.card.card-desc { position: relative; width: 90%; margin-left: auto; margin-right: auto; }

.card.card-desc p { margin: 0; }

@media (min-width: 768px) { .card.card-desc { width: 100%; min-height: 200px; } }

.branches .card { background-color: #333333; color: #fff; box-shadow: 0 0 1.25rem rgba(108, 118, 134, 0.1); }

.branches .card:hover { background-color: #ED1C24; }

.card-img-overlay { background-position: center; background-size: cover; background-repeat: no-repeat; }

.card-img-overlay .heading { color: #fff; }

.card-img-overlay .card-content { color: #fff; position: relative; top: 50%; transform: translateY(-50%); display: block; }

.card-img-overlay .card-content .heading { font-size: 2rem; }

@media (min-width: 992px) { .card-img-overlay .card-content .heading { font-size: 1.6rem; } }

.card { overflow: hidden; transition: all 0.2s ease-in-out; box-shadow: 0 3px 9px rgba(0, 0, 0, 0.2); }

.card:hover { transition: all 0.2s ease-in-out; box-shadow: 0 6px 30px rgba(0, 0, 0, 0.2); }

.card .card-title { color: #35ABBC; font-size: 1.2rem; margin-bottom: 0.5rem; font-weight: 500; }

.card .card-body { padding: 1.2rem; font-size: 0.9rem; }

.card .card-body .text-white * { color: #fff; }

.card-video { text-align: center; }

.card-video .fas { color: #ED1C24; border: 2px solid #fff; border-radius: 50%; padding: 9px; transition: all 0.2s ease-in-out; }

.card-video .fas:hover { color: #fff; transition: all 0.2s ease-in-out; box-shadow: 0 0 20px rgba(0, 0, 0, 0.6); }

.card-product { margin-bottom: 1.7rem; }

.card-product img { border-bottom: 1px solid #D1D3D4; }

.card-product .compare-p { margin-top: 10px; }

.warranty-call { min-height: 250px; }

.card-choose { min-height: 250px; }

.btn-secondary { color: #ED1C24; }

.btn { box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3); }

.btn:hover { box-shadow: 0 3px 9px rgba(0, 0, 0, 0.3); }

.product-select { text-align: center; margin-top: 0; display: flex; align-items: center; }

.product-select span { display: block; color: #ED1C24; text-transform: capitalize; line-height: 1.2rem; font-size: 0.75rem; }

.product-select a img { width: 100%; }

.breadcrumb { border-bottom: 1px solid #D1D3D4; }

.product-img .carousel .carousel-item { text-align: center; }

.product-img .carousel .carousel-item img { max-height: 500px; margin: 0 auto; }

.product-info { border-bottom: 1px solid #D1D3D4; }

.product-info .product-desc { border-bottom: 1px solid #D1D3D4; padding-bottom: 0.75rem; }

.product-info .product-points { margin-bottom: 0.8rem; }

