.product-info .product-points .model-variation ul li:before { content: "+"; font-weight: bold; color: #ED1C24; border-radius: 50%; display: inline-block; text-align: center; margin-right: 6px; margin-left: -10px; }

.product-info .product-points .product-title-point { color: #35ABBC; text-transform: uppercase; display: block; margin-bottom: 0.75rem; }

.product-info .product-points .col { border-right: 1px solid #D1D3D4; }

.product-info .product-points .col:last-of-type { border-right: none; }

.product-info .product-points .social-share a { font-size: 1.8rem; margin-right: 9px; color: #ED1C24; }

.scroll-bar { box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2); position: relative; z-index: 1000; background-color: #fff; }

.nav-pills .nav-item .nav-link { font-size: 1rem; border: none; font-family: "exo", sans-serif; }

.nav-pills .nav-item .nav-link.active { color: #fff; background-color: #35ABBC; }

.product-hl .scroll-nav .nav-item { margin-bottom: 0; }

.product-hl .hl-img { z-index: 100; padding: 0; }

.product-hl .hl-img img { width: 100%; }

.product-hl .hl-text { padding: 2rem 0; }

.product-hl .hl-text.text-white * { color: #fff; }

.product-hl .i-slide { min-height: 670 px; }

.product-hl .i-slide .container { height: 100%; }

.product-hl .i-slide .container .row { height: 100%; }

.product-ac { min-height: 700px; }

.product-ac .ac-text { background-color: #ED1C24; color: #fff; padding: 2rem; border-radius: 2rem; box-shadow: 0 0 20px rgba(0, 0, 0, 0.7); }

.product-ac .ac-text * { color: #fff; }

.stick-side { display: none; background: #ED1C24; color: #fff; border-top-left-radius: 12px; border-bottom-left-radius: 12px; position: fixed; right: 0; top: 50%; padding: 12px; box-shadow: 0 0 20px rgba(0, 0, 0, 0.6); z-index: 1060; }

.stick-side .link { display: block; color: #fff; text-align: center; padding: 9px 0; margin: 5px 0; }

.stick-side .link * { display: block; }

.stick-side .link i { font-size: 1.6rem; }

.stick-side .link small { font-size: .8rem; }

@media (min-width: 768px) { .stick-side { display: block; } }

.carousel-control a { background: #35ABBC; color: #fff; width: 32px; height: 32px; position: relative; opacity: 1; display: inline-block; line-height: 32px; font-size: 32px; transition: all 0.2s ease-in-out; box-shadow: none; }

.carousel-control a:hover { transition: all 0.2s ease-in-out; box-shadow: 0 4px 15px rgba(237, 28, 36, 0.35); transform: scale(1.2); }

.accordion { margin-bottom: 50px; }

.accordion .card-header { cursor: pointer; color: #35ABBC; }

.accordion .accordion-heading { border-bottom: 2px solid #ED1C24; }

.navbar-search { position: fixed; }

.heading { font-weight: bold; }

html, body { overflow-x: hidden; /* Prevent scroll on narrow devices */ font-size: 96%; position: relative; }

